<template>
  <div v-theme:column="'narrow'" id="show-blogs">
    <h1>博客总览</h1>
    <input type="text" placeholder="搜索" v-model="search">
    <div v-for="blog in filterBlogs" class="single-blog">
      <router-link :to="/blog/+blog.id"><h2 v-rainbow>{{blog.title | toUpperCase}}</h2></router-link>
      <article>
        {{blog.content | snippet}}
      </article>
    </div>
  </div>
</template>

<script>
export default {
  name: "ShowBlogs",
  data(){
    return{
      blogs:[],
      search:''
    }
  },
  created(){
    this.$http.get("https://my-blog-d71f2-default-rtdb.firebaseio.com/posts.json")
    .then(({data})=>{
      // this.blogs = data.slice(0,10)
      return data
    })
    .then((data)=>{
      const blogsArray =[]
      for(let key in data){
        data[key].id = key
        blogsArray.push(data[key])
      }
      this.blogs = blogsArray
      console.log(this.blogs)
    })
  },
  computed:{
    filterBlogs(){
      return this.blogs.filter((blog)=>{
        return blog.title.match(this.search)
      })
    }
  },
  filters:{
    toUpperCase(blogTitle){
      return blogTitle.toUpperCase()
    },
    snippet(blogContent){
      return blogContent.slice(0,100)+"..."
    }
  },
  directives:{
    "rainbow":{
      bind(el,binding,vnode){
        el.style.color = "#"+ Math.random().toString(16).slice(2,8)
      }
    },
    "theme":{
      bind(el,binding,vnode){
        if(binding.value =='wide'){
          el.style.maxWidth ="1260px"
        }
        else if(binding.value == "narrow"){
          el.style.maxWidth = "560px"
        }
        if(binding.arg =='column'){
          el.style.background = "#6677cc"
          el.style.padding = "20px"
        }
      }
    }
  }
}
</script>

<style scoped>
#show-blogs{
  max-width: 800px;
  margin: 0 auto;
}
.single-blog{
  padding: 20px;
  margin: 20px 0;
  box-sizing: border-box;
  background: #eee;
  border: 1px dotted #aaa;
}
#show-blogs a{
  color: #444;
  text-decoration: none;
}
input[type = 'text']{
  padding: 8px;
  width: 100%;
  box-sizing: border-box;
}
</style>



