<template>
  <div id="single-blog">
    <h1>{{blog.title}}</h1>
    <article>{{blog.content}}</article>
    <p>作者：{{blog.author}}</p>
    <p>分类：{{blog.Category}}</p>
    <ul>
      <li v-for="category in blog.Categories">
        {{category.key}}
      </li>
    </ul>
  </div>
</template>

<script>
export default {
  name: "SingleBlog",
  data(){
    return{
      id:this.$route.params.id,
      blog:{}
    }
  },
  created() {

    this.$http.get('https://my-blog-d71f2-default-rtdb.firebaseio.com/posts/'+this.id + '.json')
      .then(({data})=>{
        console.log(data)
        return data
      })
    .then(data=>{
      this.blog = data
    })
  },
}
</script>

<style scoped>
#single-blog{
  max-width: 960px;
  margin: 0 auto;
  padding: 20px;
  border: 1px dotted #aaa;
  background: #eee;
}
</style>



