<template>
  <div id="add-blog">
    <h2>添加博客</h2>
    <form v-if="submmited">
      <label for="">博客标题</label>
      <input type="text" v-model="blog.title" required>
      <label for="">博客内容</label>
      <textarea v-model="blog.content"></textarea>
    </form>
    <div id="checkboxes" >
      <span v-for="item in blog.Categories">
        <label for="">{{item.key}}</label>
        <input type="checkbox" :value="item.key" v-model="blog.Category">
      </span>
    </div>
    <div id="authors">
      <label for="">作者：</label>
      <select v-model="blog.author">
        <option v-for="author in blog.authors">{{author}}</option>
      </select>
      <button @click.prevent="postBlog">添加博客</button>
      <div v-if="!submmited">
        <h3>您的博客发送成功！</h3>
      </div>
    </div>
    <div id="preview">
      <h3>博客总览</h3>
      <p>博客标题：{{blog.title}}</p>
      <p>博客内容：</p>
      <p>{{blog.content}}</p>
      <p>博客分类：</p>
      <ul>
        <li v-for="item in blog.Category">
          {{item}}
        </li>
      </ul>
      <p>作者：{{blog.author}}</p>
    </div>
  </div>
</template>

<script>
export default {
  // https://jsonplaceholder.typicode.com/
  name: 'AddBlog',
  data () {
    return {
      blog:{
        title:'',
        content:"",
        Category:[],
        Categories: [
          {key: "Vue.js"},
          {key: "Node.js"},
          {key: "React.js"},
          {key: "Angular.js"}
        ],
        author:"",
        authors:["Sucy","Cathy","Jony"]
      },
      submmited:true
    }
  },
  methods:{
    postBlog(){
      this.$http.post("https://my-blog-d71f2-default-rtdb.firebaseio.com/posts.json",this.blog).then((data)=> {
        console.log(data)
        this.submmited = false
      })
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
#add-blog *{
  box-sizing: border-box;
}
#add-blog{
  margin: 20px auto;
  max-width: 600px;
  padding: 20px;
}
label{
  display: block;
  margin:20px 0 10px;
}
input[type="text"],textarea,select{
  display: block;
  width: 100%;
  padding: 8px;
}
#checkboxes label{
  display: inline-block;
  margin-top: 0;
}
textarea{
  height: 200px;
}
#checkboxes input{
  display: inline-block;
  margin-right: 10px;
}
button{
  display: block;
  margin: 20px 0;
  background: crimson;
  color: #fff;
  border: 0;
  padding: 14px;
  border-radius: 4px;
  font-size: 18px;
  cursor: pointer;
}
#preview{
  padding: 10px 20px;
  border: 1px dotted #ccc;
  margin: 30px 0;
}
h3{
  margin-top: 10px;
}
</style>



